import React, { useState, useEffect } from "react";
import styled from "styled-components";

const Placeholder = () => {
  return (
    <ListItem>
      There are no favorites yet. Favorite a joke to display them here.
    </ListItem>
  );
};

export default Placeholder;

const ListItem = styled.li`
  color: #cecece;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 120px;
  background: white;
  padding: 20px;
  list-style-type: none;
  border-radius: 14px;
  -webkit-box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.1);
  -moz-box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.1);
  box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.1);
`;
